import React, { Component } from 'react'
import Loader from '../Loader/Loader';
import "./CharList.scss"
import { motion } from "framer-motion"

export default class CharList extends Component {
  state = {
    characters: null,
    selectedId: null,
  }


  handleMovieClick = (e) => {
    let clickedChar = this.state.characters.find((elem) => elem.char_id == e.target.id);
    let index = this.state.characters.indexOf(clickedChar)
    this.props.history.push(`/characters/${index + 1}`)
  }

  componentDidMount() {
    let url = 'https://www.breakingbadapi.com/api/characters'
    fetch(url).then(res => {
      return res.json()
    }).then(characters => {
      console.log(characters)
      this.setState({
        characters: characters
      })
    })
  }

  render() {
    return (
      <div className="char-list__outer">
        <div className="char-list__search">
          Find Character
        </div>
        <div className="char-list">
          {this.state.characters ? this.state.characters.map((char, index) => {
            return <motion.div
              initial={{ opacity: 0 }} animate={{ opacity: 1 }} transition={{ duration: 0.2 }}
              onClick={(e) => this.handleMovieClick(e)}
              key={char.name}
              className="char-list__item">
              <div
                id={char.char_id}
                style={{ backgroundImage: `url(${char.img})` }} className="char-list__image">
                <p className="char-list__name"><span>{char.name}</span></p>
              </div>
            </motion.div>
          }) : <Loader />}
        </div>
      </div>
    )
  }
}

