import React, { Component } from 'react'
import "./SelectedChar.scss"
import { motion } from "framer-motion"
import Loader from '../Loader/Loader'

export default class SelectedChar extends Component {

  state = {
    oneChar: null
  }

  componentDidMount() {
    this.getOneCharacter()
  }

  getOneCharacter = () => {
    const { id } = this.props.match.params
    let url = `https://www.breakingbadapi.com/api/characters/${id}`
    fetch(url).then(res => {
      return res.json()
    }).then(character => {
      console.log(character)
      this.setState({
        oneChar: character[0]
      })
    })
  }

  render() {
    const { id } = this.props.match.params

    if (this.state.oneChar) {
      return (<motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} transition={{ duration: 0.5 }} className="selected-char">
        <div className="selected-char__card item-card">
          <div className="item-card__image">
            <img src={this.state.oneChar.img} alt="Something" />
          </div>
          <div className="item-card__info">
            <p>Name: <span>{this.state.oneChar.name}</span></p>
            <p>Portrayed: <span>{this.state.oneChar.portrayed}</span></p>
            <p>Nickname: <span>{this.state.oneChar.nickname}</span></p>
            <p>Status: <span>{this.state.oneChar.status}</span></p>
            <p>Occupation: <span>{this.state.oneChar.occupation.map((item) => <li>{item}</li>)}</span></p>
            <p>Birth Day: <span>{this.state.oneChar.birthday}</span></p>
            <p>Season Appearance: <span>{this.state.oneChar.appearance.map((season) => <li>{season}</li>)}</span></p>
          </div>
        </div>
      </motion.div>)
    } else {
      return (
        <Loader />
      )
    }
  }
}


